.App {
  text-align: center;
  margin-top: 50px;
}

.button {
  background-color: #4CAF50; /* Green color */
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  margin-top: 20px;
  margin-right: 10px;
  transition: background-color 0.3s, transform 0.1s; /* Smooth transition for color and transform */
}

.button:hover {
  background-color: #367c39; /* Darker green on hover */
}

.button:active {
  transform: scale(0.98); /* Slightly smaller on press */
}


/* Essential styles for the experiment layout */
.experimentLayout {
  display: flex;
  justify-content: center; /* Center the content */
  padding-left: 0; /* Ensure no padding on the left */
  margin-left: 0;
}
.taskDescription {
  position: absolute; /* or 'fixed' if you want it relative to the viewport */
  top: 0; /* Adjust this value as needed */
  width: 100%; /* Optional, for full width */
  text-align: center; /* Optional, for center alignment */
}


/* Styles for the content section with instructions */
.leftMarginContent {
  position: fixed; /* Fixed positioning relative to the viewport */
  top: 0; /* Align to the top */
  left: 9px; /* Align to the left */
  width: 20%; /* Adjust the width as needed */
  font-size: 14px;
  text-align: left;
  z-index: -10; /* Ensure it's above other content */
}


/* Styles for the main experiment content including scatterplot */
.experimentContent {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 800px; /* Adjust based on your layout */
  height: 600px;
}

.bottom-controls {
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%; 
  text-align: center; 
  padding: 10px; 


}

/* Container for the scatterplot */
.scatterplotContainer {
  width: 400px; /* Adjust as needed */
  height: 400px; /* Adjust as needed */
  position: center; /* For positioning any overlays or controls */
}