.next-button {
    background-color: #000000; /* Black color */
    color: white;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    margin-top: 1px;
    margin-left: 30px;
    transition: background-color 0.3s, transform 0.1s; /* Smooth transition */
  }
  
  .next-button:hover {
    background-color: #333333; /* Darker shade on hover */
  }
  
  .next-button:active {
    transform: scale(0.98); /* Slightly smaller on press */
  }
  